import React from 'react'
import aboutcorona from '../images/aboutcorona.jpg';
const About = ()=>{
    return(
        <>
            <div className="container-fluid about pt-5 pb-5">
            <div className="container  mt-5 ">
                <div className="about-haeader pb-5">
                    <h1 className="text-center ">About COVID-19</h1>
                </div>

                <div className="row w-100">
                     <div className="col-12 col-md-6 col-lg-5 pb-3">
                        <img src={aboutcorona} alt="ABout Corona" className="img-fluid"/>
                     </div>
                     <div className="col-12 col-md-6 col-lg-7 pb-3">
                       <div className="about-corona">
                            <h2>What is COVID-19 (Corona-Virus) ?</h2>
                            <p>Coronavirus disease (COVID-19) is an infectious disease caused by a newly discovered coronavirus. This new virus and disease were unknown before the outbreak began in Wuhan, China, in December 2019</p>
                            <p>Coronaviruses are a large family of viruses which may cause illness in animals or humans.  In humans, several coronaviruses are known to cause respiratory infections ranging from the common cold to more severe diseases such as Middle East Respiratory Syndrome (MERS) and Severe Acute Respiratory Syndrome (SARS). The most recently discovered coronavirus causes coronavirus disease COVID-19.</p>
                       </div>
                     </div>
                </div>
            </div>
            </div>
        </>
    )
}

export  default About;